import extend from 'extend'
import VueCookie from 'vue-cookie'
import {
  randRange
} from '@packages/utils/src/randText'
import axios from 'axios'
// import {
//   warn
// } from '@packages/log'

const options = {
  debug: true,
  // 
  // false
  //  profile/index.js
  profile: {
    // 
    imgDomain: ''
  },

  // 
  // false
  //  libs/index.js
  libs: {
    // CKEDITOR
    ckeditor: null
  },

  // 
  // false
  // 
  plugins: {},

  // 
  // false
  filters: {
    date: true,
    currency: true,
    value: true,
    url: true
  },

  // 
  // false
  // 
  components: {
    // false
    editor: {},

    // false
    messageBox: {},

    // Modalfalse
    modal: {},

    // Vuefalse
    notify: {},

    // /false
    // props
    upload: {
      // false
      upload: {},

      // false
      // uploadimageUpload
      imageUpload: {}
    },

    // Vuefalse
    loader: {}
  },
  axios: {
    defaults: {},
    interceptor: {
      applicationForm: true,
      dataToUnderline: true,
      disableCache: true,
      errorHandle: true,
      loading: true,
      authMac: {
        MAC_ID: '2YotnFZFEjr1zCsicMWpAA',
        MAC_KEY: 'mac_key',
        SESSION_ID: 'session_id',
        decode: true,
        getMacKey() {
          let macKey = VueCookie.get(this.MAC_KEY) || ''
          return this.decode ? decodeURIComponent(macKey) : macKey
        },
        getSessionId() {
          let sessionId = VueCookie.get(this.SESSION_ID) || ''
          return this.decode ? decodeURIComponent(sessionId) : sessionId
        }
      },
      removeEmpty: true,
      returnResponseData: true
    },
    transformResponse: {
      hump: true
    }
  },
  // 
  // false
  third: {
    qiniu: {
      httpRequest: {
        /**
         * URL
         * @param {*} type
         */
        qiniuTokenUrl(type) {
          return `/v1.0/common/${type}/qiniu_token`
        },
        /**
         * 
         * http://192.168.1.135:9001/eam/swagger-ui.html#!/HomeController/getQiniuTokenUsingGET
         * @param {*} type 1-
         */
        getServerConfig(type = 1) {
          return axios({
            url: this.qiniuTokenUrl(type),
            method: 'GET',
            responseType: 'json'
          }).then(data => {
            let datas = data.data;
            return {
              domain: datas.domainUrl,
              zoneUrl: this.getZoneUrl(datas.qiniuZone.upUrls),
              token: datas.token
            }
          })
        },
        /**
         * 
         * @param {*} qiniuZone
         */
        getZoneUrl(qiniuZone) {
          if (Array.isArray(qiniuZone)) {
            let len = qiniuZone.length
            return qiniuZone[randRange(0, len - 1)]
          }
          return String(qiniuZone)
        }
      }
    }
  },

  // 
  // false
  utils: {
    fmt: {
      // 
      date: {
        format: 'yyyy-MM-dd hh:mm:ss'
      },
      // 
      currency: {
        places: 2,
        symbol: '',
        thousand: ', ',
        decimal: '.'
      }
    },
    // hmac 
    authMac: {
      requestContentSymbol: '\n',
      authMacSymbol: 'MAC',
      nonceLen: 8
    }
  }
}

export const merger = opts => {
  return extend(true, options, opts)
}

export default options


