import eachOwn from './eachOwn'

/**
 * datanull/undefined/''
 * @param data
 */
export default (data) => {
  eachOwn(data, (value, key, data) => {
    if (value === undefined || value === null || (typeof value === 'string' && value.trim() === '')) {
      delete data[key]
    }
  })
  return data
}



