/**
 * call or apply
 * @param context 
 * @param fn 
 * @param argsArr 
 */
export default (context, fn, argsArr) => {
  if (argsArr.length <= 2) {
    fn.call(context, argsArr[0], argsArr[1])
  } else {
    fn.apply(context, argsArr)
  }
}



