import eachOwn from './eachOwn'

/**
 * options key-value  key-{default: value}
 * props
 */
export default (options = {}) => {
  let props = {}
  eachOwn(options, (value, key) => {
    props[key] = {
      default: value
    }
  })
  return props
}



