/**
 * 
 * @param obj 
 * @param fn fn(obj[key], key, obj)
 */
export default (obj, fn) => {
  for (let key in obj) {
    if (Object.prototype.hasOwnProperty.call(obj, key)) {
      fn(obj[key], key, obj)
    }
  }
}


